  font-size: 24px;
  line-height: 28px;
  color: #040404; }

.count {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 35px;
  height: 35px;
  margin-right: 13px;
  background-image: url("../img/circle.svg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  font-weight: 700;
  font-size: 20px;
  line-height: 23px;
  text-align: center;
  color: #ED5E42; }

@media (min-width: 768px) {
