.header {
  background-color: #ED5E42;
  position: sticky;
  top: 0;
  padding: 8px 15px 7px; }

.header__logo {
  width: 113px; }

.header__wrapper {
  display: flex;
  justify-content: space-between; }

@media (min-width: 768px) {
  .header {
    padding: 45px 60px 12px;
    background-color: transparent;
    margin-bottom: -98px;
    transition: 0.5s ease; }
    .header--scroll {
      background-color: #ED5E42;
      padding: 12px 60px 12px; }
  .header__logo {
    width: 187px; } }

@media (min-width: 1024px) {
  .header__wrapper {
    max-width: 1100px;
    margin: auto; } }

.social {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex; }

.social__item {
  margin-left: 12px; }

.social__link {
  display: block;
  width: 25px;
  height: 25px; }

@media (min-width: 768px) {
  .social__link {
    width: 40px;
    height: 40px; }
    .social__link svg {
      width: 40px;
      height: 40px;
      fill: #FF9E27; } }

.banner {
  background-image: url("../img/bg-image.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover; }

.banner-img {
  margin: auto; }
  .banner-img:hover {
    animation: shake 0.82s cubic-bezier(0.36, 0.07, 0.19, 0.97) both;
    transform: translate3d(0, 0, 0);
    backface-visibility: hidden;
    perspective: 1000px; }

@keyframes shake {
  10%, 90% {
    transform: translate3d(-1px, 0, 0); }
  20%, 80% {
    transform: translate3d(2px, 0, 0); }
  30%, 50%, 70% {
    transform: translate3d(-4px, 0, 0); }
  40%, 60% {
    transform: translate3d(4px, 0, 0); } }

.navigation {
  position: sticky;
  top: 40px;
  background-color: #fff; }

.navigation__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  overflow-x: auto;
  overflow-y: hidden;
  white-space: nowrap;
  padding: 15px; }

.navigation__item:not(:last-child) {
  margin-right: 50px; }

.navigation__link {
  color: #919DA3;
  font-weight: 700;
  text-decoration: none; }
  .navigation__link--active {
    color: #ED5E42; }

@media (min-width: 768px) {
  .navigation {
    top: 65px; } }

@media (min-width: 1024px) {
  .navigation {
    margin: auto; }
  .navigation__list {
    overflow-x: visible;
    overflow-y: visible;
    justify-content: center;
    padding: 40px 15px; }
  .navigation__item:not(:last-child) {
    margin-right: 20px; } }

